*,
body {
    box-sizing: border-box;
    left: 0;
    margin: 0;
    padding: 0;
    top: 0; }

body {
    background-color: $background-section-one-light;
    font-size: 16px;
    font-family: $thirdfont; }

section {
    height: 100vh;
    position: relative;
    width: 100%;
    @include breakpoint-down(small) {
        height: 99vh; } }

footer {
    height: 50vh; }

h1 {
    color: $white;
    font-family: $primaryfont;
    font-size: 7vw;
    letter-spacing: .2vw;
    text-align: center;
    @include breakpoint-down(medium) {
        font-size: 9vh; }
    @include breakpoint-down(small) {
        font-size: 7vh; } }

h2 {
    font-weight: 500; }

ul {
    list-style: none; }

p {
    font-family: $thirdfont;
    // font-family: $secondaryfont
    font-size: 2rem; }

a {
    color: $white;
    text-decoration: none;

    &:hover {
        animation: $font-c-animation; } }

video {
    height: 130%; }

// Helperclasses
.f {
    display: flex; }

.bg-color {
    background-color: $background-section-one-light; }

.fade {
    animation: bg-colorfade  20s ease-in-out infinite; }

.full {
    height: 100%;
    width: 100%; }

.abso {
    position: absolute; }

.half {
    height: 100%;
    width: 50%; }

.half-right {
    @include breakpoint-up(large) {
        margin-left: 50%;
        position: absolute; } }

.center {
    align-items: center;
    justify-content: center; }

.center-bottom {
    align-items: center; }


.black {
    background-color: $black; }

.textcontainer {
    margin: auto;
    padding: 10%;
    @include breakpoint-down(medium) {
        padding: 5%; }
    @include breakpoint-down(small) {
        padding: 5%; }

    .txt-cursive {
        color: $background-section-two-light;
        font-size: 1.5vw;
        font-style: italic;
        font-weight: 100;
        padding-top: 2%;
        text-transform: uper;

        @include breakpoint-down(medium) {
            font-size: 2.3vw; }

        @include breakpoint-down(small) {
            font-size: 3vw; } } }
//Visibility for mobile and desktop

.hide-for-mobile {
    @include breakpoint-down(medium) {
        display: none; } }

.hide-for-desktop {
    @include breakpoint-up(large) {
        display: none; } }

.half-width-viewportsm {
    @include breakpoint-down(medium) {
        height: 50%;
        width: 100%; }
    @include breakpoint-down(small) {
        height: 50%;
        width: 100%; } }

.half-height-viewportsm {
    @include breakpoint-down(medium) {
        height: 100%;
        width: 50%; } }
