footer {
    color: $white;
    font-size: 1.1vw;

    @include breakpoint-down(small) {
        font-size: 2vw; }

    .fullgrid {
        display: grid; }

    .footer-container {

        justify-self: center;

        @include breakpoint-up(large) {
            display: grid;
            grid-template-columns: 1% 94% 5%;
            grid-template-rows: 90% auto;
            grid-template-areas: "sidebar info totop" "sidebar impr totop";
            column-gap: 5em;
            row-gap: 2em;
            align-self: center; }

        @include breakpoint-down(medium) {
            display: grid;
            grid-template-columns: auto 85%;
            grid-template-rows: auto auto auto;
            grid-template-areas: "sidebar totop" "sidebar info"  "impr impr";
            column-gap: 5em;
            row-gap: 1em; }


        @include breakpoint-down(small) {
            display: grid;
            grid-template-columns: auto 85%;
            grid-template-rows: auto auto auto;
            grid-template-areas: "sidebar totop" "sidebar info"  "impr impr";
            column-gap: 5em;
            row-gap: 1em;
            padding: 5%; }

        aside {
            grid-area: sidebar;
            width: fit-content;
            align-self: center;
            animation: $font-c-animation;

            li {
                @include breakpoint-up(large) {
                    font-size: 1em;
                    font-weight: 900;
                    text-transform: uppercase;
                    text-align: center; }

                @include breakpoint-down(medium) {
                    font-size: 2em;
                    font-weight: 900;
                    text-transform: uppercase;
                    text-align: center; }

                @include breakpoint-down(small) {
                    font-size: 2em;
                    font-weight: 900;
                    text-transform: uppercase;
                    text-align: center; }
                span {
                    margin: 20%; } } }

        .inf {
            @include breakpoint-up(large) {
                grid-area: info;
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-rows: 1fr;
                column-gap: 5em;
                row-gap: 2em; }

            @include breakpoint-down(medium) {
                grid-area: info;
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto; }

            .service {

                ul {
                    width: 100%;

                    @include breakpoint-up(large) {
                        margin-top: 10%; } }

                li {
                    margin: 3% 3% 3% 0;

                    @include breakpoint-down(medium) {
                        display: inline-block;
                        margin: 1%; } } }

            .contact {

                ul {
                    @include breakpoint-up(large) {
                        margin-top: 10%; }

                    @include breakpoint-down(medium) {
                        display: inline-block; } } } }

        .folus {

            ul {
                width: 100%;
                display: flex;
                justify-content: space-between;

                li {
                    display: inline-block;

                    @include breakpoint-up(large) {
                        margin-top: 10%; }

                    @include breakpoint-down(medium) {
                        margin-top: 1%; } } } }
        .impr {
            height: auto;
            width: 100%;
            grid-area: impr;
            align-self: end;
            justify-self: end;

            ul {
                display: flex;
                justify-content: space-between;

                li {

                    display: inline-block;
                    justify-content: space-around; } } } }

    .totop {
        @include breakpoint-up(large) {
            grid-area: totop;
            align-self: end;
            justify-self: end; }
        @include breakpoint-down(medium) {
            grid-area: totop;
            align-self: center;
            justify-self: center; }

        svg {
            width: 4em;

            @include breakpoint-down(medium) {
                width: 6em; }

            .svgtotop {

                animation: fill-colorfade  20s ease-in-out infinite; } } } }

@keyframes fill-colorfade {
    from {
        fill: $fade-a; }

    25% {
        fill: $fade-b; }

    50% {
        fill: $fade-c; }

    75% {
        fill: $fade-e; }

    to {
        fill: $fade-a; } }
